require("dotenv").config();
var Testrail = require('testrail-api');


export async function getautomationmanualcount(Baseurl,Testrailuser,APIkey,projectId,suiteId,limit,offset){
  var automated = 0, manual = 0;
  var testrail = new Testrail({
    host: Baseurl,
    user: Testrailuser,
    password: APIkey
  });
await testrail.getCases(projectId,{suite_id:suiteId,limit:limit,offset:offset} )
  .then(function (result) {
    automated += result.body.cases.map(x => x.custom_automation_type).filter(x => [2,5].includes(x)).length;
    manual += result.body.cases.map(x => x.custom_automation_type).filter(x => [0,1,3,4,6].includes(x)).length;
    console.log(`update: automated[${automated}] not[${manual}]`);
  }).catch(function (error) {
    console.log('error', error.message);
  });
  return {automated,manual}
}
  
  


  