
const axios = require('axios');
const https = require('https');



export async function getJiraopenbugs(Baseurl,jirauser,jirakey,jiraprojectcode,Isbearertoken){
    var total=0;
    if (Isbearertoken) {
        var api = await axios.create({
            baseURL: Baseurl,
            timeout: 10000,
            auth: {
                username: jirauser,
                password: jirakey
            },
            httpsAgent: new https.Agent({  
                rejectUnauthorized: false
              })
        });
    }
    else {
        var api = await axios.create({
            baseURL: Baseurl,
            timeout: 10000,
            headers: {
                'Authorization': "Bearer " + jirakey
            },
            httpsAgent: new https.Agent({  
                rejectUnauthorized: false
              })
        });
    }
   
    
await api.get(`/rest/api/2/search?jql=project=${jiraprojectcode} and issuetype = bug and status not in (Closed,done,resolved)`)
    .then((response) => {
        if(response.data.total===0){
             total = 0;
            console.log("Open Bug count: " + total);      
        }else{
             total = response.data.total;
            console.log("Open Bug count : " + total);
        }
    })
    .catch((err) => {
        console.log(`oh no!: ${err}`);
    })

    return {total};
}
