var Testrail = require('testrail-api');

export async function getpassfail(Baseurl,Testrailuser,APIkey,projectId,runId,limit,offset){
    var pass = 0, fail = 0;
    var testrail = new Testrail({
      host: Baseurl,
      user: Testrailuser,
      password: APIkey
    });
  await testrail.getRun(runId)
    .then(function (result) {
        pass+=result.body.passed_count;
        fail+=result.body.failed_count;
    }).catch(function (error) {
      console.log('error', error.message);
    });
    await testrail.getPlans(projectId,{limit:limit,offset:offset})
    .then(function (result) {
        var localpass=result.body.plans.map((x) => x.passed_count);
        var localfail=result.body.plans.map((x) => x.failed_count);
        if (result.body.plans.length !== 0) {
            pass+= localpass.reduce((previousValue, currentValue) => previousValue + currentValue, 0);
            fail+= localfail.reduce((previousValue, currentValue) => previousValue + currentValue, 0);
          }
    }).catch(function (error) {
      console.log('error message for plans', error.message);
    });
    return {pass,fail}
  }
  